module.exports = "## 组件模板\r\n\r\n### 内容替换\r\n\r\n组件初始化时有效，组件的属性值替换对应的占位符\r\n\r\n```html\r\n${!xxx}\r\n```\r\n\r\n### script\r\n\r\n导入模板作用域模块\r\n\r\n```html\r\n<script type=\"dojo/require\">\r\n    at: \"dojox/mvc/at\",\r\n    lang: \"dojo/_base/lang\"\r\n</script>\r\n```\r\n\r\n### 属性\r\n\r\n属性 | 说明\r\n:---------------|:---------------\r\ndata-dojo-type | 组件\r\ndata-dojo-props | 组件参数\r\ndata-dojo-mixins | 组件混入类\r\ndata-dojo-attach-point | 给元素或子组件注册引用信息。类似于 vue 的 ref, `this.xxx`\r\ndata-dojo-attach-event | 普通元素绑定 dom 事件 `data-dojo-attach-event=\"onxxx: onXxx\"`\r\n\r\n示例代码\r\n\r\n```html\n﻿<div>\r\n    <script type=\"dojo/require\">\r\n        at: \"dojo/_base/lang\"\r\n    </script>\r\n    <h2>普通 html 元素</h2>\r\n    <button data-dojo-attach-point=\"btn\" data-dojo-attach-event=\"onclick: onbtnClick\">click</button>\r\n    <h2>dojo 控件, 有 data-dojo-type 属性</h2>\r\n    <button data-dojo-type=\"dijit/form/Button\" data-dojo-props=\"iconClass:'icon icon-printer',onClick:lang.hitch(this,'onbtnClick')\" data-dojo-attach-point=\"btn2\">click</button>\r\n    <p>\r\n        功能混合 data-dojo-mixins\r\n    </p>\r\n    <input data-dojo-type=\"dijit/form/TextBox\" data-dojo-props=\"label:'name'\" data-dojo-mixins=\"desktop/widgets/ItemField\">\r\n</div>\n```"

